@import "../../global.scss";

.portfolio{
    background-color: rgb(255, 251, 210);
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;

        @include mobile{
            font-size: 20px;
            margin-top: 30px;
        }
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile{
            margin: 10px 0;
            flex-wrap: wrap; //wrap the menu
            justify-content: center; //center menu
        }
    
        // li{
        //     font-size: 14px;
        //     margin-right: 50px;
        //     padding: 7px;
        //     border-radius: 10px;
        //     cursor: pointer;
            
        //     &.active{
        //         background-color: $mainColor;
        //         color: white;
        //     }
        // }
    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        //if item dont go into container, item will go below
        flex-wrap: wrap;

        @include mobile{
            width: 100%;
        }

        .item{
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid lightgray;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease; //transition when hover cursor
            cursor: pointer;

            @include mobile{
                width: 130px;
                height: 100px;
            }

            h3{
                position: absolute;
                font-size: 20px; //h3 at back
            }

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1; //image infront, h3 back
            }

            &:hover{ //when hover to img, text appear
                background-color: $mainColor;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
    
}